@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
@font-face {
  font-family: 'icomoon';
  src: url(../../static/media/icomoon.9ddb20f3.eot);
  src: url(../../static/media/icomoon.9ddb20f3.eot#iefix) format('embedded-opentype'),
    url(../../static/media/icomoon.29f14f7e.ttf) format('truetype'),
    url(../../static/media/icomoon.639fd916.woff) format('woff'),
    url(../../static/media/icomoon.c4c74027.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-snowy:before {
  content: "\e900";
}

.icon-rainy:before {
  content: "\e901";
}

.icon-cloud:before {
  content: "\e902";
}

.icon-moon:before {
  content: "\e903";
}

.icon-cloud1:before {
  content: "\e904";
}

.icon-cloudy:before {
  content: "\e905";
}

.icon-sun:before {
  content: "\e906";
}
*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

