.App {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(to top, #a8ff78, #78ffd6);
}

.App__wrapper {
  max-width: 700px;
  margin: 0 auto;
  padding: 0 1.5625rem 1.5625rem;
}

.App.hot {
  background: linear-gradient(to top, #fbd400, #f97c00);
}

.App.rain {
  background: linear-gradient(to top, #bdc3c7, #2c3e50);
}

.App.cold {
  background: linear-gradient(to top, #1CB5E0, #000046);
}

.App.clouds {
  background: linear-gradient(to bottom, #bdcad9 0%, #e1dada 74%);
}

.App.night {
  background: linear-gradient(to top, #01426D 0%, #001D37 74%);
}
.spinner {
  width: 100%;
}

.spinner__image {
  display: block;
  margin: 0 auto;
}
.location {
  color: white;
  font-size: 2.1875rem;
  text-align: center;
  text-shadow: 2px 2px rgba(58, 53, 53, 0.73);
  cursor: default;
}
.weather {
  display: flex;
  justify-content: space-between;

  margin: 1.5rem auto;
  padding: 1rem;
  width: 100%;

  font-size: 4rem;
  font-weight: 700;
  color: white;
  text-align: left;
  text-shadow: 2px 2px rgba(58, 53, 53, 0.73);

  background-color: rgb(0, 0, 0, 0.1);
  border-radius: 16px;
  box-shadow: 0.1875rem 0.1875rem rgba(58, 53, 53, 0.73);
  cursor: default;
}

.weather__description {
  font-size: 2rem;
  margin-bottom: .5rem;
}

.weather__description:last-of-type {
  margin-bottom: 0;
}

.weather__icon {
  font-size: 6rem;
  text-align: right;
}

@media (min-width: 450px) {
  .weather {
    padding: 2rem;
  }
}

@media (min-width: 576px) {
  .weather {
    padding: 2rem 3rem;
  }
}
.header {
  position: relative;

  display: flex;
  align-items: center;
  margin-bottom: 3rem;
  width: 100%;
  height: 6rem;
  background-color: rgba(255, 255, 255, .5);
  box-shadow: 0px 2px rgba(129, 123, 123, 0.3);
}

.header__container {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 2.5rem;
}

.header__logo {
  font-family: Roboto;
  font-size: 3.2rem;
  text-shadow: 2px 2px rgba(58, 53, 53, 0.73);
  color: white;
  cursor: default;
}

.header__search {
  width: 100%;
  margin-left: 11rem;
  margin-right: 18%;
}

.header__toggle {
  display: block;
  width: 44px;
  height: 44px;

  font-family: Roboto;
  color: white;
  font-size: 2.4rem;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 2px 2px rgba(58, 53, 53, 0.73);
  border: 1px solid rgb(185, 184, 184);
  border-radius: 7px;
  background-color: transparent;
  box-shadow: 0 0 2px rgba(129, 123, 123, 0.3);
  cursor: pointer;
}

@media (max-width: 768px) {
  .header__search {
    margin: 0 10%;
  }
}

@media (max-width: 576px) {
  .header {
    margin-bottom: 10rem;
  }

}

@media (max-width: 576px) {
  .header__search {
    position: absolute;
    bottom: -7rem;
    left: 40%;
    transform: translateX(-50%);
    width: 90%;
  }
}
.search {
  position: relative;
  width: 100%;
}

.search__bar {
  color: black;
  font-size: 1.5rem;
  display: block;
  width: 100%;
  padding: 1.5rem;
  border: none;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 15px;
  box-shadow: 0px 2px rgba(129, 123, 123, 0.3);
  background: rgba(255, 255, 255, 0.52);
  transition: 0.4s ease;
}

.search .search__bar:focus {
  background-color: white;
}

.search__icon {
  position: absolute;
  top: 11px;
  right: 14px;
  width: 24px;
  height: 24px;
  background: url(../../static/media/search.bd0f7c30.svg) center/ contain no-repeat;
}
